<template>
  <div id="app">
    <header>
      <span class="bop-script">It's a Girl</span>
    </header>
    <main>
      <!-- <img src="./assets/logo.png" alt="Vue.js PWA"> -->
      <router-view></router-view>
    </main>
  </div>
</template>

<script>
export default {
  name: 'app'
}
</script>

<style>
@font-face {
  font-family: 'BirdsOfParadise';
  src: url('./assets/bop.ttf');
}

.bop-script {
  font-family: 'BirdsOfParadise';
  font-size: 2.48em;
}

html {
  background: #ff69bf url('./assets/background.png') repeat left top;
}

body {
  margin: 0 auto;
  width: 1800px;
}

#app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: #2c3e50;
}

main {
  text-align: center;
  margin-top: 40px;
  //background: #fff url('./assets/flowers2.png') no-repeat left top;
  //background-size: 100% auto;
  //position: relative;
  display: flex;
}

main > #invite-text {
  width: 800px;
}

main > img {
  width: 500px;
}

#invite-text {
  display: flex;
  flex-flow: column;
  justify-content: space-between;
  margin: 8em 0;
}

#invite-text span {
  font-size: 1.48em;
  display: flex;
  justify-content: space-between;
  align-content: center;
  flex-flow: column;
}

#invite-text span.bop-script {
  font-size: 4em;
  line-height: 1.9em;
}

header {
  margin: 0;
  height: 56px;
  padding: 0 16px 0 24px;
  background-color: #35495E;
  color: #ffffff;
}

header span {
  display: block;
  position: relative;
  font-size: 20px;
  line-height: 1;
  letter-spacing: .02em;
  font-weight: 400;
  box-sizing: border-box;
  padding-top: 16px;
}
</style>



